require('../scss/ashlee.scss');

var styles = [];

if (module.hot)
{
    module.hot.accept();

    document.querySelectorAll('link').forEach((el,i) => {
        if (! styles[i]) styles[i] = el.href;
        el.href = styles[i] + "?t=" + Date.now();
    })
}


